<template>
  <div style="margin-top: 100px;" class="d-flex flex-row">
    <div style="margin-left: 20px;">
      <Panel />
    </div>
    <Loader v-if="isLoading" />
    <div class="bookmarks">
      <div class="bookmarks__left">
        <div v-if="dataPosts.length > 0">
          <v-card class="bookmarks__title">
            <h2>Закладки</h2>
          </v-card>
          <div v-for="(user, index) of dataPosts" :key="index">
            <v-card style="padding: 20px; margin-bottom: 30px;">
            <div
              class="account__post__top d-flex flex-row justify-space-between align-center"
              style="border-bottom: 5px;"
            >
              <div class="d-flex flex-row align-center">
                <v-img
                  v-if="isImg && user.additionally.img"
                  :src="'/' + user.additionally.img"
                  class="account__post__image"
                ></v-img>
                <div v-else class="account__post__image"></div>
                <h2 @click="$router.push({name: 'Account', params: {id: user.authorId}})">
                  {{ user.firstname }} {{ user.lastname }}
                </h2>
                <v-chip
                  color="myPurple"
                  style="cursor: pointer; color: #fff !important;"
                  class="ma-2"
                  v-if="user.category"
                >
                  {{ user.category }}
                </v-chip>
                <v-tooltip top>
                  <template v-slot:activator="{ on }">
                    <v-chip
                      v-on="on"
                      class="account__post__level"
                      color="myPurple"
                    >
                      {{ user.authorLevel }} level
                    </v-chip>
                  </template>
                  <span>{{ user.rank }}</span>
                </v-tooltip>
              </div>
              <h3 class="account__post__date">{{ user.date }}</h3>
              <v-menu offset-y>
                <template v-slot:activator="{ on }">
                  <v-icon color="purple accent-4" dark v-on="on">menu</v-icon>
                </template>
                <v-list>
                  <v-list-item
                    class="account__post__edit"
                    @click="postDelete(user)"
                  >
                    <v-list-item-title>Удалить из закладок</v-list-item-title>
                  </v-list-item>
                </v-list>
              </v-menu>
            </div>
            <div class="account__post__text" v-html="user.text"></div>
            <div v-if="user.images">
              <div 
                style="margin-bottom: 30px" 
                v-for="(img, index) of user.images" 
                :key="index"
              >
                <v-img max-height="300" :src="'/' + img"></v-img>
              </div>
            </div>
            <div class="account__post__bottom d-flex flex-row">
              <div
                class="account__post__stats account__post__likes d-flex flex-row"
                @click="addLike(user)"
              >
                <v-icon>favorite</v-icon>
                <h3 v-if="user.countLikes">{{ user.countLikes }}</h3>
                <h3 v-else>0</h3>
              </div>
              <div
                class="account__post__stats account__post__views d-flex flex-row"
                :title="user.views + ' Просмотров'"
              >
                <v-icon>visibility</v-icon>
                <h3>{{ user.views }}</h3>
              </div>
            </div>
            </v-card>
          </div>
        </div>
        <div 
          v-if="dataPosts.length == 0 && !isLoading" 
          class="bookmarks__nobookmarks d-flex flex-row align-center"
        >
          <h2>Вы не сохранили ни одного поста в закладки</h2>
          <v-img src="../assets/error.jpg" width="200" height="200"></v-img>
        </div>
      </div>
      <div class="bookmarks__right"></div>
    </div>
  </div>
</template>

<script>
import Panel from "../components/Panel.vue";
import Loader from "../components/Loader.vue";
import axios from "axios";
import "../scss/posts.scss";

export default {
  name: "Bookmarks",
  metaInfo: {
    title: "MyOcean - Мои закладки"
  },
  components: { Panel, Loader },
  data() {
    return {
      dataPosts: [],
      user: "",
      isLoading: true,
      isImg: false
    };
  },
  async created() {
    this.user = JSON.parse(localStorage.getItem("user"))
    if (!this.user) return this.$router.push("/")
    try {
      if (this.user.bookmarks.length != 0) {
        let info = await axios.get("/api/posts/get_bookmarks/" + this.user.bookmarks)
        for (let user of info.data) {
          let author = await axios.get("/api/users/get_authors/" + user.authorId)
          let obj = Object.assign(author.data, user)
          this.dataPosts.push(obj)
          this.isImg = true
        }
      }
      this.isLoading = false
    } catch(err) {
      this.isLoading = false
    }
  },
  methods: {
    async postDelete(post) {
      let res = await axios.delete("/api/users/post_delete/" + post.id + '/' + this.user.id)
      localStorage.setItem("user", JSON.stringify(res.data))
    }
  }
};
</script>

<style lang="scss" scoped>
.bookmarks {
  width: 1170px;
  &__left {
    padding: 20px;
    width: 700px;
    min-height: 580px;
  }
  &__nobookmarks {
    padding: 20px;
    background-color: #fff;
    font-size: 18px;
    border-radius: 6px;
  }
  &__title {
    font-size: 24px;
    padding: 20px;
    margin-bottom: 50px;
  }
}
</style>
